.modal-is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.modal-backdrop {
  width: 100%;
  height: 100%;
  z-index: 9998;
  transition-property: opacity, visibility, transform;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  transition-delay: 0;
  background: rgba(0, 0, 0, .2);
  position: fixed;
  top: 0;
  overflow-y: scroll;
  transform: scale(100%);
}

.modal-backdrop.modal-is-hidden .modal-window {
  transition-property: opacity, visibility, transform;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  transition-delay: 0;
  transform: scale(0)skewY(-90deg);
}

.modal-window {
  max-width: 280px;
  width: 100%;
  text-align: center;
  transition-property: opacity, visibility, transform;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  transition-delay: 0;
  background-color: #fcfcfc;
  border: 2px solid #f8d24e;
  border-radius: 10px;
  padding: 28px;
  position: absolute;
  top: 10%;
  left: 50%;
  transform: translateX(-50%)scale(100%);
}

